import React, {useEffect} from 'react';
import './Stylesheet/body.css';
import {biodata, description, skills, projects, contact} from './Resources/data.js';

export const Data = () => {
    return ( <div id = "details">
    {Object.keys(biodata).map((key, index) => {
        return (<div>
        <span className = "key">{key} : </span>
        <span className = "value">{biodata[key]}</span>
        <br/></div>);
    })}
    </div>);
}

export const Profile = () => {

    const content = [
        {"title" : "Hi.","content" : "intro"},
        {"title" : "What I develop","content" : "product"}
    ]
    const reArrange = () => {
        let component = document.getElementById('bioContainer');
        let width = window.innerWidth;
        let row = (width < 1400) ? 1 : 2;
        component.style.gridTemplateColumns = `repeat(${row},1fr)`;
        component.style.marginTop = (width < 1400) ? "750px" : "350px";
        component.style.textAlign = "center";
    }

    useEffect(()=>reArrange(),[])
    window.addEventListener('resize', () => reArrange());
    return (<div> 
        <div className = "title" style = {{paddingTop : "50px"}}> <h1>Profile</h1> </div>
        <div id = "profile">
            <div id="banner"/>
            <img id = "dp" alt = "display-picture"
            src = {require("./Resources/Images/profile.png")} />
            <Data/>
            <div id = "bioContainer"> {content.map((value, index) => {
                return( <div style = {{textAlign : "left", margin : "auto", 
                padding : "20px", minWidth : "300px", maxWidth : "900px"}}> 
                    <h1>{value.title}</h1> {<p>{description[value.content]}</p>}
                </div>)
            })} </div>
        </div>    
    </div>);
}

export const Skills = () => {    
    const icon_note = [
        {"icon" : "verified",
         "note" : "I am certified in the corresponding course"},
         {"icon" : "comprehend",
         "note" : "I can comprehend the corresponding language"},
         {"icon" : "read",
         "note" : "I can read scripts written in the corresponding language"},
         {"icon" : "write",
         "note" : "I can write scripts in the corresponding language"}
    ]
    const asserticons = (flag) => {
        const display = (char) => {
            let icon;
            switch(char){
                case "v": icon = "verified"; break;
                case 'c': icon = "comprehend"; break;
                case 'r': icon = "read"; break;
                case 'w': icon = "write"; break;
                default : return;
            }
            const background = require(`./Resources/Images/${icon}.png`);
            return <img src = {background} alt = "asserticon" 
                width = "15px" style = {{ paddingLeft: "10px"}}/>
        }
        return Array.from(flag).map((char, index) => display(char));
    }

    const ratings = (count) => {
        let type = []; type.length = 5;
        for(let i = 0; i<5; i++) type[i] = i < count ? "checked" : "unchecked";
        return (<div className = "stars">
            { type.map((value,index) => {
                const background = require(`./Resources/Images/star(${value}).png`);
                return <img src = {background} alt = "ratings" width = "15px"/>
            })}
        </div>);
    }
    return( <div> <div className = "title"><h1>Skills</h1> </div>
        <div id = "skills">
            <div id = "table">
                <div id = "header"><span>skill</span><span>ratings</span></div>
                {Object.keys(skills).map((value1, index) => {
                    return(<div id = "content">
                    <span className = "category">* {value1}</span>
                        {Object.keys(skills[value1]).map((value2, index) => { 
                            return(<div className = "row">
                                <div className = "skillname">  
                                    <span>{value2}</span>{asserticons(skills[value1][value2][0])}
                                </div> 
                                {ratings(skills[value1][value2][1])}
                            </div>)
                        })}
                    </div>)
                })}
            </div>
            <span style = {{fontSize : "20px", fontWeight : "bold"}}>- note :</span><br/>
            <div id = "note">
                <span><b>*</b> The ratings are self evaluated</span><br/>
                <span><b>*</b> The symbols in the table indicate the following :</span><br/>
                {icon_note.map((value, index) => {
                    const background = require(`./Resources/Images/${value.icon}.png`);
                    return(<div>
                        <img src = {background} alt = {value.icon}/>
                        <span> : {value.note}</span>
                    </div>)
                })}
            </div>
        </div>    
    </div>);
}

export const Projects = () => {
    const reArrange = () => {
        let component = document.getElementById('projects');
        let width = window.innerWidth;
        let row = (width > 1400) ? 3 : (width > 800) ? 2 : 1;
        component.style.gridTemplateColumns = `repeat(${row},1fr)`;
        let images = document.getElementsByClassName('images') 
        width = window.getComputedStyle(images[0]).getPropertyValue("width");
        width = parseInt(width.replace("px",""));
        [ ...images ].forEach(image => image.style.height = (width * 2/3).toString()+"px")
            
        
    }
    useEffect(()=> reArrange(),[])
    window.addEventListener('resize', () => reArrange());                
    return (<div> <div className = "title"><h1>Projects</h1> </div>
        <div id = "projects">
            {projects.map((value, index) => {
                const background = require(`./Resources/Images/ProjectIcons/${value.name}.png`);
                return ( <a href ={value.link}
                    className = "images" style = {{backgroundImage : `url(${background})`}}>
                        <img src = {require(`./Resources/Images/ProjectIcons/goto.png`)}/>
                        <div className = "overlay"/>
                </a>
            )})}  
        </div>    
    </div>);
}

export const Contact = () => {
    return(<div> <div className = "title"><h1>Contact</h1> </div>
        <div id = "contact">
            <div id = "para">
                <h1>I can help.</h1> {<p>{description.extro}</p>}
            </div>
            {Object.keys(contact).map((key, index) => { return (<div>
                <span className = "key">{key} : </span>
                <span className = "value">{contact[key]}</span>
            <br/></div>);})} 
            <img id = "stamp" alt ="stamp" src = {require("./Resources/Images/stamp.png")}/> 
        </div>        
    </div>)
}