import React,{useEffect} from 'react';
import {Profile, Skills, Projects, Contact} from './body';
import {handles} from './Resources/data.js';
import './Stylesheet/app.css';

export const Navbar = () => {
  let titles = ["profile","skills", "projects","contact"]
  const scrollTo = (value) => {
    let pos = document.getElementById(value).offsetTop -180;
    if(value == "profile") pos = 0;
    window.scrollTo(0, pos);
  }

  const updateNavbar = () => {
    let pos = []; pos.length = titles.length;
    let index = 0; 
    for(let i = 1; i< titles.length ; i++){
      pos[i] = (i==0)? 0 : document.getElementById(titles[i]).offsetTop - 210*i;
      if( pos[i] < window.scrollY) index = i;
    }
    let buttons = document.getElementsByClassName("navButtons")
    for(let i = 0; i< pos.length; i++)
      buttons[i].style.borderBottom = (i == index) ? "5px solid #00AE86":"none";     
  }
  useEffect(() => updateNavbar(), []);
  window.addEventListener('scroll', (event) => updateNavbar());
  return(<div id = "navbar"> <div>
      {titles.map((value, index)=>{return (
        <button className = "navButtons" onClick = { () => scrollTo(value)}>{value}</button>)})}
  </div> </div>);
}
export const Handles = () => {
  return ( <div id = "handles"> <div>
      {handles.map((value, index) => {
        return  <a href={value.link}>
                  <img src= {require(`./Resources/Images/handles/${value.name}.png`)} 
                  alt={value} padding="1vw"/>
                </a>
      })}
  </div> </div>);
}

export const App = () => {
  return ( <div id = "app">
    <Navbar/> 
    <div id = "container">
    
    <Profile/>
    <Skills/>
    <Projects/>
    <Contact/>
    </div>
    <Handles/>  
  </div>);
}